import Link from 'next/link';

const navigation = {
  main: [
    { name: 'الرئيسية', href: '/' },
    { name: 'سياسة الخصوصية', href: '/privacy-policy' },
    { name: 'الشروط والأحكام', href: '/terms' },
    { name: 'اتصل بنا', href: '/contact' },
    { name: 'الأسئلة الشائعة', href: '/faq' },
  ],
};

export default function Footer() {
  return (
    <footer className="bg-white border-t border-gray-200">
      <div className="mx-auto max-w-7xl overflow-hidden px-6 py-10 sm:py-12 lg:px-8">
        <nav className="flex flex-wrap justify-center gap-x-8 gap-y-4" aria-label="Footer">
          {navigation.main.map((item) => (
            <Link
              key={item.name}
              href={item.href}
              className="text-sm leading-6 text-gray-600 hover:text-indigo-600"
            >
              {item.name}
            </Link>
          ))}
        </nav>
        
        <div className="mt-8 border-t border-gray-900/10 pt-8">
          <p className="text-xs leading-5 text-gray-500 text-center">
            المنصة غير مسؤولة عن نوعية وسوء نية الأعمال التي يتم طلبها أو إنجازها.
            نحاول منع أي تجاوزات ويمكنكم مساعدتنا بالضغط على جرس الإبلاغ إذا رأيتم ما يريب.
          </p>
        </div>
        
        <p className="mt-10 text-center text-xs leading-5 text-gray-500">
          &copy; {new Date().getFullYear()} عَرَبَاي ArabAi. جميع الحقوق محفوظة.
        </p>
      </div>
    </footer>
  );
} 