// العثور على مقال عشوائي ذو معرف مختلف عن المقال الحالي
const getRandomRelatedPost = (currentId: number) => {
  const filteredPosts = blogPosts.filter(post => post.id !== currentId);
  if (filteredPosts.length === 0) return null;
  return filteredPosts[Math.floor(Math.random() * filteredPosts.length)];
};

export default function BlogPostPage() {
  const params = useParams();
  const [post, setPost] = useState<any>(null);
  const [relatedPost, setRelatedPost] = useState<any>(null);
  const [isLoading, setIsLoading] = useState(true);
  
  useEffect(() => {
    // العثور على المقال بناءً على المعرف
    const postId = Array.isArray(params.id) ? parseInt(params.id[0]) : parseInt(params.id as string);
    const foundPost = blogPosts.find(p => p.id === postId);
    
    if (foundPost) {
      setPost(foundPost);
      setRelatedPost(getRandomRelatedPost(postId));
    }
    
    setIsLoading(false);
  }, [params.id]);
  
  if (isLoading) {
    return (
      <div className="min-h-screen flex flex-col">
        <Navbar />
        <main className="flex-grow flex items-center justify-center">
          <div className="text-center">
            <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-indigo-600 mx-auto"></div>
            <p className="mt-4 text-gray-600">جاري تحميل المقال...</p>
          </div>
        </main>
        <Footer />
      </div>
    );
  }
  
  if (!post) {
    return (
      <div className="min-h-screen flex flex-col">
        <Navbar />
        <main className="flex-grow bg-gray-50">
          <div className="max-w-3xl mx-auto py-16 px-4 sm:px-6 lg:px-8 text-center">
            <h1 className="text-3xl font-bold text-gray-900 mb-4">لم يتم العثور على المقال</h1>
            <p className="text-xl text-gray-600 mb-8">عذرًا، المقال الذي تبحث عنه غير موجود.</p>
            <Link href="/blog" className="inline-flex items-center px-5 py-2 border border-transparent text-base font-medium rounded-full shadow-sm text-white bg-indigo-600 hover:bg-indigo-700">
              العودة إلى المدونة
            </Link>
          </div>
        </main>
        <Footer />
      </div>
    );
  }
  
  return (
    <div className="min-h-screen flex flex-col">
      <Navbar />
      
      <main className="flex-grow bg-gray-50">
        {/* رأس المقال */}
        <div className="bg-indigo-700 text-white py-16">
          <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
            <div className="flex items-center justify-center space-x-2 space-x-reverse mb-4">
              <Link href="/blog" className="text-indigo-100 hover:text-white text-sm font-medium">المدونة</Link>
              <ChevronRightIcon className="h-4 w-4 text-indigo-200" />
              <span className="text-sm font-medium">{post.category}</span>
            </div>
            
            <h1 className="text-3xl md:text-4xl font-bold mb-6">{post.title}</h1>
            
            <p className="text-xl text-indigo-100 mb-8">{post.excerpt}</p>
            
            <div className="flex items-center justify-center space-x-6 space-x-reverse text-sm">
              <div className="flex items-center">
                <UserIcon className="h-4 w-4 ml-1" />
                <span>{post.author}</span>
              </div>
              <div className="flex items-center">
                <CalendarIcon className="h-4 w-4 ml-1" />
                <span>{post.date}</span>
              </div>
              <div className="flex items-center">
                <ClockIcon className="h-4 w-4 ml-1" />
                <span>{post.readTime}</span>
              </div>
            </div>
          </div>
        </div>
        
        {/* صورة الغلاف */}
        <div className="max-w-4xl mx-auto -mt-16 px-4 sm:px-6 lg:px-8 relative z-10">
          <div className="w-full h-72 md:h-96 relative rounded-lg overflow-hidden shadow-xl">
            <Image 
              src={post.coverImage} 
              alt={post.title} 
              fill
              className="object-cover"
            />
          </div>
        </div>
        
        {/* محتوى المقال */}
        <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
          <article>
            {/* الوسوم */}
            <div className="flex flex-wrap gap-2 mb-8">
              {post.tags.map((tag: string) => (
                <span
                  key={tag}
                  className="px-3 py-1 bg-indigo-100 text-indigo-800 text-sm rounded-full"
                >
                  {tag}
                </span>
              ))}
            </div>
            
            {/* المحتوى الرئيسي للمقال */}
            <div 
              className="prose prose-lg max-w-none prose-headings:text-gray-900 prose-a:text-indigo-600"
              dangerouslySetInnerHTML={{ __html: post.content }}
            />
            
            {/* مشاركة المقال */}
            <div className="mt-12 pt-8 border-t border-gray-200">
              <h3 className="text-lg font-medium text-gray-900 mb-4">شارك هذا المقال</h3>
              
              <div className="flex space-x-4 space-x-reverse">
                <button className="p-2 bg-blue-500 text-white rounded-full hover:bg-blue-600">
                  <ShareIcon className="h-5 w-5" />
                </button>
                {/* يمكن إضافة المزيد من أزرار المشاركة هنا */}
              </div>
            </div>
          </article>
          
          {/* معلومات الكاتب */}
          <div className="mt-12 pt-8 border-t border-gray-200">
            <div className="flex items-center space-x-4 space-x-reverse">
              <div className="flex-shrink-0">
                <div className="h-12 w-12 rounded-full bg-indigo-100 flex items-center justify-center">
                  <UserIcon className="h-6 w-6 text-indigo-600" />
                </div>
              </div>
              <div>
                <h3 className="text-lg font-medium text-gray-900">{post.author}</h3>
                <p className="text-gray-600">كاتب متخصص في مجال الذكاء الاصطناعي وتطبيقاته في مختلف المجالات.</p>
              </div>
            </div>
          </div>
        </div>
        
        {/* مقالات ذات صلة */}
        {relatedPost && (
          <div className="bg-white py-12">
            <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8">
              <h2 className="text-2xl font-bold text-gray-900 mb-8">مقالات ذات صلة</h2>
              
              <div className="bg-gray-50 rounded-xl overflow-hidden">
                <div className="md:flex">
                  <div className="md:flex-shrink-0 relative h-60 md:h-auto md:w-1/3">
                    <Image
                      src={relatedPost.coverImage}
                      alt={relatedPost.title}
                      fill
                      className="object-cover"
                    />
                  </div>
                  <div className="p-6 md:w-2/3">
                    <div className="flex items-center text-sm text-gray-500 mb-2">
                      <span className="bg-indigo-100 text-indigo-800 text-xs px-2 py-1 rounded-full">
                        {relatedPost.category}
                      </span>
                      <span className="mx-2">•</span>
                      <ClockIcon className="h-4 w-4 ml-1" />
                      <span>{relatedPost.readTime}</span>
                    </div>
                    <Link href={`/blog/${relatedPost.id}`} className="block mt-2">
                      <h3 className="text-xl font-semibold text-gray-900 hover:text-indigo-600">
                        {relatedPost.title}
                      </h3>
                      <p className="mt-3 text-gray-600">
                        {relatedPost.excerpt}
                      </p>
                    </Link>
                    <div className="mt-4">
                      <span className="text-sm text-gray-500">{relatedPost.date}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        )}
        
        {/* دعوة للعمل */}
        <div className="bg-indigo-700 py-12">
          <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
            <h2 className="text-2xl font-bold text-white mb-4">هل ترغب في تعلم المزيد عن الذكاء الاصطناعي؟</h2>
            <p className="text-indigo-100 mb-6">
              انضم إلى نشرتنا البريدية للحصول على أحدث المقالات والنصائح حول تقنيات الذكاء الاصطناعي.
            </p>
            <div className="max-w-md mx-auto">
              <div className="flex">
                <input
                  type="email"
                  placeholder="البريد الإلكتروني"
                  className="w-full px-5 py-3 rounded-r-md focus:outline-none focus:ring-2 focus:ring-white focus:border-transparent"
                />
                <button className="px-5 py-3 bg-white text-indigo-700 font-bold rounded-l-md hover:bg-indigo-50">
                  اشتراك
                </button>
              </div>
            </div>
          </div>
        </div>
      </main>
      
      <Footer />
    </div>
  );
} 